const Photos = require("googlephotos");

const oauth2Client = new google.auth.OAuth2(
  "273568515719-516mua007e3nt41g0reeg5nc5fmqtmut.apps.googleusercontent.com",
  "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",
  "https://drive.google.com"
);

const scopes = [Photos.Scopes.READ_ONLY, Photos.Scopes.SHARING];

const url = oauth2Client.generateAuthUrl({
  // 'online' (default) or 'offline' (gets refresh_token)
  access_type: "online",

  scope: scopes
});

console.log(url)

function MediaLibrary() {
  const service = useContext(PhotoServiceContext); // new GooglePhotosService();
  const [albums, setAlbums] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  // useEffect only runs once to get the promise data initially.
  useEffect(
    function () {
      const promise = service.loadAlbums();
      promise.then(function (arg) {
        setAlbums(arg);
        setIsLoading(false);
      });
    },
    [service]
  );

  const newAlbums = albums.map(function (obj) {
    return (
      <li key={obj.id}>
        <Link to={'/album/' + obj.id}>
          <figure>
            <img src={obj.coverPhotoBaseUrl} alt="" />
            <figcaption>
              <h3>{obj.title}</h3>
            </figcaption>
          </figure>
        </Link>
        <br />
      </li>
    );
  });

  return (
    <FamilyDetailsWrapper>
      <ContentHeader headerTitle="Daga Gallery" />
      {isLoading && 'Loading...'}
      {albums.length > 0 && (
        <div>
          {/* console.log('AlbumsList albums', albums) */}


          <ul className="albums">{newAlbums}</ul>
        </div>
      )}

       <hr />
      <Link to="/">Back to Albums List</Link>
    </FamilyDetailsWrapper>
  );
}

export default MediaLibrary;
